import React from 'react';
import { Box, Typography } from '@material-ui/core';
import useStyles from '../../styles/common/stepperDescription';

const StepperDescription = (props) => {
  const classes = useStyles();
  const { items } = props;
  return (
    <Box>
      {items.map((item, index) => {
        return (
          <Box mb={-1.5}>
            <Typography key={index} paragraph variant="body1" align='justify'
              className={classes.description}
            >
              {item}
            </Typography>
          </Box>
        );
      })}
    </Box>
  )
};

export default StepperDescription;