import React from 'react';
import { Button } from '@material-ui/core';
import useStyles from '../../styles/intro/intro';

const onDownload = () => {
  const link = document.createElement("a");
  // link.download = 'MyCV.pdf';
  link.target = "_blank";
  link.href = "MyCV.pdf";
  link.click();
};

const CV = (props) => {
  const classes = useStyles();
  return (
    <Button
      variant="outlined"
      color="secondary"
      size="large"
      onClick={onDownload}
      className={classes.cv}
      {...props}
    >
      Download CV
    </Button>
  );
};

export default CV;