import React from 'react';
import { Grid, Box, Hidden, Paper, Typography } from '@material-ui/core';
import Rating from '@material-ui/lab/Rating';
import SkillImage from './SkillImage';
import Tag from '../common/Tag';
import useStyles from '../../styles/skills';

const Skill = (props) => {
  const { title, url, level, rate } = props;
  const classes = useStyles();
  return (
    <Grid item md={12} style={{ width: '100%' }}>
      <Box component={Paper} p={1}>
        <Grid container spacing={1}>
          <Grid container item alignContent='center' lg={1} md={1} sm={1} xs={2}>
            <SkillImage
              title={title}
              url={url}
            />
          </Grid>
          <Grid container alignContent="center" item lg={4} md={4} sm={4} xs={5}>
            <Box alignItems="center" justifyContent="center" mt={0.2} className={classes.skillTitleBox}>
              <Typography variant="h6" className={classes.skillTitle}>
                {title}
              </Typography>
            </Box>
          </Grid>
          <Hidden xsDown>
            <Grid container item justifyContent='center' alignContent='center'
              lg={4} md={4} sm={4}
            >
              <Tag
                TagIcon={null}
                label={level}
                img=''
              />
            </Grid>
          </Hidden>
          <Grid container item justifyContent='center' alignContent='center'
            lg={3} md={3} sm={3} xs={5}
          >
            <Tag
              TagIcon={null}
              label={
                <Box pt={0.4}>
                  <Rating
                    alignContent='center'
                    name="read-only"
                    value={rate}
                    size="small"
                    className={classes.rating}
                    // style={{ 'fontSize': '3rem' }}
                    readOnly
                  />
                </Box>}
              img=''
            />
          </Grid>
        </Grid>
      </Box >
    </Grid >
  );
};

export default Skill;