import React from 'react';
import {
  Box, Paper, Typography, Divider,
  Stepper, Step, StepLabel, StepContent,
} from '@material-ui/core';
import StepperLabel from '../common/StepperLabel';
import StepperDescription from '../common/StepperDescription';
import TwoColList from '../common/TwoColList';
import ThreeColList from '../common/ThreeColList';
import useStyles from '../../styles/education';

const EducationStepper = (props) => {
  const classes = useStyles();
  const { title, Icon, data } = props;
  return (
    <Box component={Paper} height="100%" className={classes.box}>
      <Typography align="center" variant="h4" gutterBottom
        className={classes.title}
      >
        {title}
      </Typography>
      <Divider light variant="middle" />
      <Stepper orientation="vertical" className={classes.stepper}>
        {data.map((item, index) => {
          const {
            title, date, program, gpa,
            description, achievements, courses
          } = item;
          return (
            <Step key={index} active expanded completed>
              <StepLabel
                StepIconComponent={() => (<Icon color="secondary" />)}
              >
                <StepperLabel
                  title={title} date={date}
                  detail={program} extraDetail={gpa}
                  md11={9} md12={3} md21={9} md22={3}
                  sm11={9} sm12={3} sm21={9} sm22={3}
                  xs11={8} xs12={4} xs21={9} xs22={3}
                />
              </StepLabel>
              <StepContent>
                <StepperDescription
                  items={description}
                />
                {achievements.length !== 0 ?
                  <Typography className={classes.topic}>Achievements:</Typography> : null}
                <TwoColList
                  items={achievements}
                  mt={-1} ml={-1} mr={-2}
                  md1={9} md2={3}
                  sm1={9} sm2={3}
                  xs1={9} xs2={3}
                />
                {courses.length !== 0 ?
                  <Typography className={classes.topic}>Related Courses:</Typography> : null}
                <ThreeColList
                  items={courses}
                  mt={-1} ml={-1} mr={-2} mb={1}
                  md1={6} md2={4} md3={2}
                  sm1={6} sm2={4} sm3={2}
                  xs1={6} xs2={4} xs3={2}
                />
              </StepContent>
            </Step>
          )
        })}
      </Stepper>
    </Box >
  );
};

export default EducationStepper;