import React from 'react';
import { NavHashLink } from 'react-router-hash-link';
import {
  Box, Typography, Container,
  AppBar, Toolbar, Hidden, Link, IconButton,
  Drawer, Divider, List,
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import useStyles from '../styles/header';

const navItems = ['Home', 'About', 'Education', 'Experiences', 'Courses', 'Skills', 'Projects'];

const CustomLink = (props) => (
  <Box mx={2}>
    <Typography color="inherit" component={NavHashLink}
      {...props}
    />
  </Box>
);

const Header = (props) => {
  const classes = useStyles();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const { window } = props;
  const container = window !== undefined ? () => window().document.body : undefined;

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Container onClick={handleDrawerToggle} mx={2}>
      <Box display="flex" flexDirection="row" alignItems='center' my={0.5}>
        <IconButton color="inherit" onClick={handleDrawerToggle} className={classes.name}>
          <MenuIcon />
        </IconButton>
        <Typography variant="h4" underline="none"
          href="http://localhost:3000/"
          className={classes.name}
        >
          Sepehr Babapour
        </Typography>
      </Box>
      <Divider />
      <List>
        {navItems.map((item, index) => (
          <Box my={3}>
            <CustomLink
              to={'#' + item.toLowerCase()}
              activeClassName={classes.activeLink}
              className={classes.link}
              key={index}
            >
              {item}
            </CustomLink>
          </Box>
        )
        )}
      </List>
    </Container>
  );
  return (
    <AppBar color="primary" className={classes.header}>
      <Container>
        <Toolbar component="nav" className={classes.toolbar} disableGutters>
          <Hidden mdUp>
            <IconButton color="inherit" onClick={handleDrawerToggle}>
              <MenuIcon />
            </IconButton>
            <Drawer
              classes={{ paper: classes.drawer }}
              container={container}
              variant="temporary"
              open={mobileOpen}
              onClose={handleDrawerToggle}
              ModalProps={{
                keepMounted: true,
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <Box className={classes.title}>
            <Typography component={Link} variant="h4" underline="none"
              href="http://localhost:3000/"
              className={classes.name}
            >
              Sepehr Babapour
            </Typography>
          </Box>
          <Hidden smDown>
            {navItems.map((item, index) => (
              <CustomLink
                to={'#' + item.toLowerCase()}
                activeClassName={classes.activeLink}
                className={classes.link}
                key={index}
              >
                {item}
              </CustomLink>
            )
            )}
          </Hidden>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;