import React from 'react';
import {
  Grid, Box, Paper, Typography, Divider,
  Stepper, Step, StepLabel, StepContent,
} from '@material-ui/core';
import StepperLabel from '../common/StepperLabel';
import StepperDescription from '../common/StepperDescription';
import AppLinksList from '../common/AppLinksList';
import LargeTagsList from '../common/LargeTagsList'
import useStyles from '../../styles/experiences';

const ProjectStepper = (props) => {
  const classes = useStyles();
  const { title, Icon, data } = props;
  return (
    <Box component={Paper} height="100%" p={2} className={classes.box}>
      <Typography align="center" variant="h4" gutterBottom
        className={classes.title}
      >
        {title}
      </Typography>
      <Divider light variant="middle" />
      <Stepper orientation="vertical" className={classes.stepper}>
        {data.map((item, index) => {
          const { title, date, program, instructor, description, tags, links, skills } = item;
          return (
            <Step key={index} active expanded completed>
              <StepLabel
                StepIconComponent={() => (<Icon color="secondary" />)}
              >
                <StepperLabel
                  title={title} date={date}
                  detail={program} extraDetail={instructor}
                  md11={10} md12={2} md21={10} md22={2}
                  sm11={10} sm12={2} sm21={10} sm22={2}
                  xs11={10} xs12={2} xs21={9} xs22={3}
                />
              </StepLabel>
              <StepContent>
                <Box className={classes.tagBox}>
                  <Box mt={1} mb={2} mr={-1}>
                    <Grid container justifyContent='space-between'>
                      <LargeTagsList
                        items={tags}
                        md={9} sm={9} xs={8}
                      />
                      <AppLinksList
                        items={links}
                        md={3} sm={3} xs={4}
                        iconClass={classes.iconClass}
                      />
                    </Grid>
                  </Box>
                  <StepperDescription
                    items={description}
                  />
                  <Box mt={3}>
                    <LargeTagsList
                      items={skills}
                      md={12}
                    />
                  </Box>
                </Box>
              </StepContent>
            </Step>
          )
        })}
      </Stepper>
    </Box >
  );
};

export default ProjectStepper;
