import { Typography } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import Typist from 'react-typist';
import useStyles from '../../styles/intro/intro';

const TypingText = () => {
  const classes = useStyles();
  const [count, setCount] = useState(1);

  useEffect(() => {
    setCount(1);
  }, [count]);

  return (
    <React.Fragment>
      {count ? (
        <Typist onTypingDone={() => setCount(0)}>
          <Typography component="span" className={classes.desc}>
            A CE Undergrad Student...
          </Typography>
          <Typist.Backspace count={27} delay={1500} />
          <Typography component="span" className={classes.desc}>
            An AI Enthusiast...
          </Typography>
          <Typist.Backspace count={21} delay={1500} />
        </Typist>
      ) : (
        <Typography component="span">&zwnj;</Typography>
      )}
    </React.Fragment>
  );
};

export default TypingText;
