import React from 'react';
import useStyles from '../../styles/skills';

const SkillImage = (props) => {
  const { url, title } = props;
  const classes = useStyles();
  return (
    <img
      style={{ borderRadius: "100%" }}
      src={url}
      alt={title}
      className={classes.skillImage}
    />
  );
};

export default SkillImage;