import React from 'react';
import { Box, Grid, Paper, Typography } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faChalkboardTeacher,
  faCalendar,
  faStar,
} from '@fortawesome/free-solid-svg-icons';
import CardLabel from '../common/CardLabel';
import AppLinksList from '../common/AppLinksList';
import useStyles from '../../styles/course';

const Course = (props) => {
  const { img, title, program, instructor, date = '', grade = '', links } = props;
  const classes = useStyles();
  return (
    <Box align="center" component={Paper} p={1}>
      <CardLabel
        img={img}
        title={title}
        program={program}
      />
      <Box p={1} mb={1}>
        <Grid container justifyContent='space-between'>
          <Grid item md={6} sm={6} xs={6}>
            <Box>
              <Box>
                <FontAwesomeIcon
                  icon={faChalkboardTeacher}
                  fixedWidth
                  className={classes.icon}
                />
              </Box>
              <Box className={classes.afterIcon}>
                <Typography color="textPrimary" component="span" className={classes.detail}>
                  {instructor}
                </Typography>
              </Box>
            </Box>
          </Grid>
          <Grid item md={6} sm={6} xs={6}>
            <Box>
              <Box>
                <FontAwesomeIcon
                  icon={grade !== '' ? faStar : faCalendar}
                  className={classes.icon}
                />
              </Box>
              <Typography color="textPrimary" className={classes.detail}>
                {grade !== '' ? 'Grade: ' + grade.value + ' / ' + grade.from : date}
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </Box>
      <Box mb={1}>
        <AppLinksList
          items={links}
          position='center'
          md={12} sm={12} xs={12}
          iconClass={classes.iconClass}
        />
      </Box>
    </Box>
  );
};

export default Course;