import React from 'react';
import { Box, Grid, Typography } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAddressCard } from '@fortawesome/free-solid-svg-icons';
import useStyles from '../../styles/about/description';

const bioFunc = (bio) => (<p>{bio}</p>)

const Description = (props) => {
  const { biographies } = props;
  const classes = useStyles();
  return (
    <Box mb={2}>
      <Grid component={Box} container alignItems="center">
        <Box mx={1}>
          <FontAwesomeIcon
            icon={faAddressCard}
            size="2x"
            className={classes.icon}
          />
        </Box>
        <Box mx={1}>
          <Typography className={classes.title}>
            A few words about me
          </Typography>
        </Box>
      </Grid>
      <Box ml={3} mb={4}>
        <Typography align='justify' className={classes.bio}>
          {biographies.map(bioFunc)}
        </Typography>
      </Box>
    </Box>
  );
};

export default Description;
