import React from 'react';
import { Box, Grid, Typography } from '@material-ui/core';
import useStyles from '../../styles/common/threeColList';

const ThreeColList = (props) => {
  const classes = useStyles();
  const {
    items, mt, ml, mr, mb,
    md1, md2, md3,
    sm1, sm2, sm3,
    xs1, xs2, xs3,
  } = props;
  return (
    <Box mt={mt} ml={ml} mr={mr} mb={mb}>
      <ul className={classes.ul}>
        {items.map((item, index) => {
          return (
            <Grid container key={index}>
              <Grid container alignContent="center" justifyContent="flex-start"
                md={md1} sm={sm1} xs={xs1}
              >
                <Box>
                  <Typography component="li" variant='body1'
                    className={classes.text}
                  >
                    {item.name}
                  </Typography>
                </Box>
              </Grid>
              <Grid container justifyContent="flex" alignContent="center"
                md={md2} sm={sm2} xs={xs2}
              >
                <Box>
                  <Typography className={classes.text}>
                    {item.prof}
                  </Typography>
                </Box>
              </Grid>
              <Grid container alignContent="center" justifyContent="flex-end"
                md={md3} sm={sm3} xs={xs3}
              >
                <Box pr={1}>
                  <Typography className={classes.text}>
                    {item.grade}
                  </Typography>
                </Box>
              </Grid>
            </Grid>
          );
        })}
      </ul>
    </Box>
  );
};

export default ThreeColList;