import React from 'react';
import { Box, Paper } from '@material-ui/core';
import CardLabel from '../common/CardLabel';
import LargeTagsList from '../common/LargeTagsList';
import AppLinksList from '../common/AppLinksList';
import useStyles from '../../styles/project';

const Course = (props) => {
  const { img, title, program, skills, links } = props;
  const classes = useStyles();
  return (
    <Box align="center" component={Paper} p={2}>
      <CardLabel
        img={img}
        title={title}
        program={program}
      />
      <Box my={2}>
        <LargeTagsList
          items={skills}
          position='center'
          md={12}
        />
      </Box>
      <AppLinksList
        items={links}
        position='center'
        md={12}
        iconClass={classes.iconClass}
      />
    </Box>
  );
};

export default Course;