import React from 'react';
import { Grid } from '@material-ui/core';
import AppLink from './AppLink'
import useStyles from '../../styles/common/appLinksList';

const AppLinksList = (props) => {
  const { items, position = 'flex-end', md = 4, sm = 4, xs = 4, iconClass } = props;
  const classes = useStyles();
  return (
    <Grid container item justifyContent={position} spacing={1} md={md} sm={sm} xs={xs}>
      {items.map((item, index) => {
        const { LinkIcon, href } = item;
        return (
          <Grid item key={index}>
            <AppLink
              href={href}
              className={classes.link}
            >
              <LinkIcon className={iconClass} />
            </AppLink>
          </Grid>
        );
      })}
    </Grid>
  )
};

export default AppLinksList;