import React from 'react';
import {
  Box, Paper, Typography, Divider,
  Stepper, Step, StepLabel, StepContent,
} from '@material-ui/core';
import StepperLabel from '../common/StepperLabel';
import InlineList from '../common/InlineList'
// import SmallTagsList from '../common/SmallTagsList';
import useStyles from '../../styles/education';

const EducationStepper = (props) => {
  const classes = useStyles();
  const { title, Icon, data } = props;
  return (
    <Box component={Paper} height="100%" className={classes.box}>
      <Typography align="center" variant="h4" gutterBottom
        className={classes.title}
      >
        {title}
      </Typography>
      <Divider light variant="middle" />
      <Stepper orientation="vertical" className={classes.stepper}>
        {data.map((item, index) => {
          const { title, date, place, instructor, achievements } = item;
          return (
            <Step key={index} active expanded completed>
              <StepLabel
                StepIconComponent={() => (<Icon color="secondary" />)}
              >
                <StepperLabel
                  title={title} date={date}
                  detail={place} extraDetail={instructor}
                  md11={8} md12={4} md21={8} md22={4}
                  sm11={8} sm12={4} sm21={8} sm22={4}
                  xs11={8} xs12={4} xs21={8} xs22={4}
                />
              </StepLabel>
              <StepContent>
                <Box mb={-2}>
                  <Box mb={1}>
                    <Typography className={classes.topic}>Highlighted Topics:</Typography>
                  </Box>
                  {/* <SmallTagsList
                    items={achievements}
                  /> */}
                  <InlineList
                    items={achievements}
                  />
                </Box>
              </StepContent>
            </Step>
          )
        })}
      </Stepper>
    </Box >
  );
};

export default EducationStepper;