import React from 'react';
import { Box, Grid, Typography } from '@material-ui/core';
import useStyles from '../../styles/common/twoColList';

const TwoColList = (props) => {
  const classes = useStyles();
  const { items, mt, ml, mr, md1, md2, sm1, sm2, xs1, xs2 } = props;
  return (
    <Box mt={mt} ml={ml} mr={mr} >
      <ul className={classes.ul}>
        {items.map((item, index) => {
          return (
            <Grid container key={index}>
              <Grid container alignContent="center" justifyContent="flex-start"
                md={md1} sm={sm1} xs={xs1}
              >
                <Box>
                  <Typography component="li" variant='body1'
                    className={classes.text}
                  >
                    {item.name}
                  </Typography>
                </Box>
              </Grid>
              <Grid container alignContent="center" justifyContent="flex-end"
                md={md2} sm={sm2} xs={xs2}
              >
                <Box pr={1}>
                  <Typography className={classes.text}>
                    {item.date}
                  </Typography>
                </Box>
              </Grid>
            </Grid>
          );
        })}
      </ul>
    </Box>
  );
};

export default TwoColList;