import React from 'react';
import { Avatar, Chip, Box } from '@material-ui/core';
import useStyles from '../../styles/common/tag';

const Tag = (props) => {
  const { TagIcon, label, img, color = '' } = props;
  const classes = useStyles();
  if (TagIcon === null || img === '') {
    return (
      <Box>
        <Chip
          variant="outlined"
          label={label}
          className={classes.textChip}
        />
      </Box>
    )
  }
  else {
    return (
      <Chip
        variant="outlined"
        avatar={
          <Avatar
            className={classes.avatar}
            src={img}
          >
            <TagIcon
              className={classes.icon}
              style={{ color: color }}
            />
          </Avatar>
        }
        label={label}
        className={classes.imgChip}
      />
    )
  }
};

export default Tag;