import React from 'react';
import { Grid } from '@material-ui/core';
import Project from './Project';
import projects from '../../data/projects';

const Projects = () => {
  const size = 7;
  let chunks = [];
  let len = projects.length;
  let parts = Math.ceil(len / size);
  for (let i = 0; i < parts; i++) {
    chunks.push(projects.slice(i * size, (i + 1) * size));
  }
  if (len % size === 3) {
    let chunk = chunks.pop();
    chunks.push(chunk.slice(0, 2));
    chunks.push(chunk.slice(2, 3));
  }
  return (
    <Grid container justifyContent='center' spacing={3}>
      {chunks.map((chunk, cIndex) => {
        console.log(chunk);
        return (
          <Grid item container justifyContent='center' spacing={3}>
            {chunk.map((project, pIndex) => {
              const { img, title, program, skills, links } = project;
              return (
                <Grid key={pIndex} item md={3} sm={6}>
                  <Project
                    img={img}
                    title={title}
                    program={program}
                    skills={skills}
                    links={links}
                  />
                </Grid>
              );
            })}
          </Grid>
        );
      })}
    </Grid>
  );
};

export default Projects;