import React from 'react';
import { Grid } from '@material-ui/core';

const LargeTagsList = (props) => {
  const { items, position = 'left', md, sm, xs } = props;
  return (
    <Grid container item alignItems='center' justifyContent={position}
      spacing={1} md={md} sm={sm} xs={xs}
    >
      {items.map((Item, index) => {
        return (
          <Grid item key={index}>
            <Item />
          </Grid>
        );
      })}
    </Grid>
  )
};

export default LargeTagsList;